// import logo from './logo.svg';
import React, { useState } from 'react';
import './App.css';
import './components/navbar.css'
import Alert from './components/Alert';
import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
// import { isDOMComponent } from 'react-dom/test-utils';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";


function App() {

const [mode, setMode] = useState('light');

// const removeBodyColor = () =>{
//   document.body.classList.remove('bg-dark');
//   document.body.classList.remove('bg-success');
//   document.body.classList.remove('bg-danger');
//   document.body.classList.remove('bg-primary');
//   document.body.classList.remove('bg-warning');
// }

const toggleMode = () =>{
  // console.log(cls);
  // removeBodyColor();
  // document.body.classList.add('bg-'+cls)
  if(mode === 'light'){
    setMode('dark');
    document.body.style.backgroundColor = '#0a0726';
    showAlert("Dark mode is anabled","success");
  }
  else {
    setMode('light');
    document.body.style.backgroundColor = 'white';
    showAlert("Light mode is anabled","success")
  }
}

const ironManMode =() => {
    if(mode === 'light'){
      setMode('danger');
      document.body.style.backgroundColor = '#de4300';
      showAlert("Iron man mode is anabled","success");
    }
    else{
      setMode('light');
      document.body.style.backgroundColor = 'white';
      showAlert("Light mode is anabled","success")
    }
}

const [alert, setAlert] = useState(null);
const showAlert = (message,type) =>{
  setAlert(
    {
      msg : message,
      type: type
    }
  )
  setTimeout(() => {
    setAlert(null);
  }, 1500);
}

  return (
    <>

      {/* <Navbar title="TextUtils" mode={mode} toggleMode={toggleMode} ironManMode={ironManMode}/>
      <Alert alert={alert}/>
      <div className="container">
      <TextForm heading="Write a text to Analyze" mode={mode} showAlert={showAlert}/>
      <About/>
      </div> */}
    <Router>
      <Navbar title="TextUtils" mode={mode} toggleMode={toggleMode} ironManMode={ironManMode}/>
      <Alert alert={alert}/>
      <div className="container">
      <Routes>
          <Route path="/about" element={<About mode={mode}/>}></Route>
          <Route path="/" element= {<TextForm heading="Write a text to Analyze" mode={mode} showAlert={showAlert}/>}></Route>
        </Routes>
      </div>
      </Router>
    </>
  );
}

export default App;
