
import React, {useState} from 'react'

export default function TextForm(props) {
        
        const [text, setText] = useState("enter your text");

        const handleChange = (event) => {
             setText(event.target.value);
        }

        const handleUpClick = () => {
                let newText = text.toUpperCase();
                setText(newText);
                props.showAlert("Converted to upper case","success");
        }
        const handleLoClick = () => {
          let newText = text.toLowerCase();
          setText(newText);
          props.showAlert("Converted to Lower case","success");
  }

  const handleRevClick = () => {
    let newText = text.split(" ").reverse().join(" ");
    setText(newText);
    props.showAlert("string Reversed","success");
}

const handleSpeechClick = ()=>{
  let msg = new SpeechSynthesisUtterance();
  msg.text = text;
  window.speechSynthesis.speak(msg);
  props.showAlert("plug in earphone","success");
}

const handleClearClick = () =>{
  let newText = "";
  setText(newText);
  props.showAlert("text field is cleared","success");
}

const handleCopy = () =>{
  
  navigator.clipboard.writeText(text);
  props.showAlert("copied to clipboared", "success")
}

  return (
    <>
    <div className="container" style={{color: props.mode ==='dark'?'white':'black'}}>
<h1>{props.heading}</h1>    
<div className="mb-3">
<textarea className="form-control" value={text} onChange={handleChange} id="myBox" rows="8" style= {{background: props.mode ==='dark'?'#0a0726':'white', color: props.mode ==='dark'?'white':'black'}}></textarea>
</div>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleUpClick}>Convert to UpperCase</button>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleLoClick}>Convert to LowerCase</button>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleRevClick}>Reverse the string</button>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleSpeechClick}>Text to Speech</button>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleClearClick}>Clear Text</button>
<button disabled={text.length === 0} className="btn btn-primary mx-1 my-1" onClick={handleCopy}>Copy Text</button>

    </div>
    <div className="container my-3" style={{color: props.mode ==='dark'?'white':'black'}}>
      <h2>Your Text Summary</h2>
      <p>{text.split(/\s+/).filter((element)=>{return element.length !==0}).length} word count and {text.length} character count</p>
      <p>{0.008*text.split(" ").filter((element)=>{return element.length !==0}).length} speed to read words</p>
      <h3>Preview</h3>
      <p>{text.length>0?text:"Nothing to preview"}</p>
    </div>
    </>

  )
}
